import { Component } from "@angular/core";
import { NavController, IonicPage } from "ionic-angular";

import { ApiEndpoint, ApiProvider } from "../../providers/api.service";

@IonicPage({
    name: "AboutPage"
})

@Component({
    selector: "page-about",
    templateUrl: "about.html"
})
export class AboutPage {

    public posts: string;
    public comments: string;

    public users: string;

    constructor(public navCtrl: NavController, private api: ApiProvider) {
        this.api.endpoints.POSTS = ApiEndpoint.endpoint("http://jsonplaceholder.typicode.com/posts");
        this.api.endpoints.USERS = ApiEndpoint.endpoint("http://jsonplaceholder.typicode.com/users");
        this.api.endpoints.COMMENTS = ApiEndpoint.endpoint("http://jsonplaceholder.typicode.com/comments");
    }

    public ionViewDidLoad () {
        this.api.get([this.api.endpoints.POSTS, this.api.endpoints.COMMENTS]).then(data => {

        }).catch(error => {
            console.error("Error.")
        });

        this.api.get(this.api.endpoints.USERS).then(data => {

        }).catch(error => {
            console.error("Error.");
        });
    }
}


